'use client'

import LoaderComp from '@/app/components/LoaderComp';
import movies from '@/app/constants/movies.json'
import Image from 'next/image';
import {  use, useState, useEffect } from 'react';
import Header from '@/app/components/Header';

interface PageInterface {
  params: Promise<{ movieId: number }>
}

const Page: React.FC<PageInterface> = ({ params }) => {
  const [isLoading, setIsLoading] = useState<boolean>(true);

  const movieId =  use(params).movieId;  
// const movieId = params.movieId;
  const movie = movies[movieId];  

  useEffect(() => {
    const timer = setTimeout(() => {
      setIsLoading(false); 
    }, 1000); 
   
    return () => clearTimeout(timer);
  }, [movieId]);

  return (
    <div>
      {isLoading ? (
        <LoaderComp /> 
      ) : (
        <>
          <Header/>
          <div className="container flex flex-col lg:flex-row items-center relative sm:px-10 md:px-20 xl:px-72">

            <div className="lg:w-1/2 grid text-lg gap-3 p-6">
              <h1 className="text-2xl sm:text-4xl font-bold">{movie.title}</h1>
              <p className="text-sm sm:text-base">Director: <span className='font-bold'>{movie.Director}</span></p>
              <div className="flex gap-4">
                <p className="text-sm sm:text-base">{movie.release}</p>
                <p className="text-sm sm:text-base">IMDB <span className='font-bold'>{movie.Rating}</span></p>
              </div>
              <ol className="flex items-center gap-2 flex-wrap">
                {movie.Category.map((ele, index) => (
                  <li className="bg-slate-200 p-1 font-bold rounded-sm text-xs sm:text-sm" key={index}>
                    {ele}
                  </li>
                ))}
              </ol>
              <p className="text-justify text-sm sm:text-base">{movie.description}</p>
            </div>

              <Image src={movie.image} width={400} height={400} alt={movie.title}
                className="object-contain w-full h-full sm:w-1/2 rounded-lg m-2 cursor-pointer hover:scale-[1.01] "
              />

          </div>
        </>
      )}
    </div>
  );
}

export default Page;